html, body {
    margin:0;
    padding:0;
    width:100%;
    height:100%;
    cursor:default;
}

::-webkit-scrollbar
{
    width:0px;
}
::-webkit-scrollbar-track-piece
{
    background-color:transparent;
}

body {
    font-family:$body-font;
    font-size:$body-size;
    color:$black;
    background:$white;
}

h1, h2, h3, h4, p {
    margin:0;
    line-height:calc(100% + 2px);

    &.red {
        color:$red;
    }
    &.orange {
        color:$orange;
    }
    &.yellow {
        color:$yellow;
    }
    &.green {
        color:$green;
    }
    &.blue {
        color:$blue;
    }
    &.purple {
        color:$purple;
    }

    &.gradient {
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        &.red {
            @include gradient('red');
        }
        &.orange {
            @include gradient('orange');
        }
        &.yellow {
            @include gradient('yellow');
        }
        &.green {
            @include gradient('green');
        }
        &.blue {
            @include gradient('blue');
        }
        &.purple {
            @include gradient('purple');
        }

        &.text-shadow {
            text-shadow:none;
            filter: drop-shadow(0px 2px 3px $gray-1);
        }
    }

    &.text-shadow {
        text-shadow:0px 2px 3px $gray-1;
    }
}

h1 {
    font-size:$h1-size;
    letter-spacing:-2px;
}

h2 {
    font-size:$h2-size;
    letter-spacing:-1px;
    padding-bottom:20px;
}

h3 {
    font-size:$h3-size;
    letter-spacing:-1px;
}

h4 {
    font-size:$h4-size;
    letter-spacing:-1px;
}

p {
    font-size:$body-size;
}

a {
    text-decoration:none;
    color:$black;

    @include animate;
    &:hover {
        color:$blue;
    }
}

img {
    max-width:100%;
    min-width: 690px;
    max-height:100%;
    width:100%;
}

section {
}

section, div {
    position:relative;
    overflow:hidden;

    .center {
        text-align:center;
    }
}